@import "../../global.scss";
.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 40px;
        width: 100%;
        text-align: center;
        height: 10%;
        

        @include mobile{
            font-size: 30px;
        }
    }
    ul{
        margin: 10px;
        list-style:none;
        display: flex;
        

        @include mobile{
            height: 30%;
            flex-wrap: wrap;
            justify-content: center;
        }
    }
    .container{
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        
        flex-wrap: wrap;
        
        @include mobile{
            width: 100%;
            
        }

        .item{
            width: 220px;
            height: 140px;
            border-radius: 20px;
            border:1px solid white;
            margin: 10px 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            color: white;
            position: relative;
            transition: all 0.5s ease;
            cursor: pointer;
           
            @include mobile{
                margin: 10px 0;
                width: 130px;
                height: 100px;
            }
           
            h3{
                position: absolute;
                font-size: 20px;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;

            }
            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }


}